.sidebar-option{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 8px 0px;
    color: #616161;
}
.sidebar label{
    font-size: 10px;
}
.sidebar-icon{
    font-size: 20px;
}
.active{
    color:#6264a7;
    border-left: 4px solid #6264a7;
}
.sidebar-option:hover{
    color:#6264a7;
}
