.item-avatar{
    height: 32px;
}
.dailer-item{
    display: flex;
    width: 100%;
    flex:1;
    padding: 6px 0px;
}
.item-info{
    display: flex;
    flex-direction: column;
    flex:1;
    margin-left: 8px;
    overflow: hidden;
}

.item-icons{
    display: flex;
}
.item-icon{
    font-size: 16px;
}
.item-title{
    font-weight: 500;
}
.item-subtitle{
    font-size: 12px;
    color:#616161;
}
